import { useState } from 'react';
import { Row, Col } from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

const BookPage = ({ book }) => {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <img src={book.thumbnail || 'https://placehold.co/100x150'}
        width='100%' onClick={handleShow} style={{ 'cursor': 'pointer' }} />

      <Modal
        size='lg'
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <Row>
            <Col xs={3}> <img src={book.thumbnail || 'https://placehold.co/100x150'} /> </Col>

            <Col className='align-self-center'>
              <div> : {book.sale_price}</div>
              <div> : {book.authors}</div>
              <div> : {book.publisher}</div>
              <div>ISBN : {book.isbn}</div>
              <div> : {book.datetime}</div>
            </Col>
          </Row>
          <hr />
          <div>{book.contents || ''}</div>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary">Understood</Button>
        </Modal.Footer>

      </Modal>
    </>
  );

}

export default BookPage
