import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Row, Col, Form, InputGroup, Button, Card } from 'react-bootstrap'
import BookPage from './BookPage';

const HomePage = () => {

    const [document, setDocument] = useState([]);             // 
    const [query, setQuery] = useState('');             // 
    const [page, setPage] = useState(1);                      // 
    const [last, setLast] = useState(1);

    const callingAPI = async () => {                        //  db !       
        const url = "https://dapi.kakao.com/v3/search/book?target=title"
        const config = {
            headers: { Authorization: "KakaoAK 56c7b0773cbedf7469d3bf02f7199522" },
            params: {
                query: query,
                size: 12,
                page: page
            }
        };
        const res = await axios.get(url, config);           // axios  
        console.log(res);                                  // db   !
        setDocument(res.data.documents);                   // db    !
        setLast(Math.ceil(res.data.meta.pageable_count / 12));
    }

    useEffect(() => { callingAPI(); }, [page]);

    const onSubmit = (e) => {
        e.preventDefault();                                 //   ,    
        if (query === '') {
            alert(" !");
        } else {
            setPage(1);                                     //   1  !
            callingAPI();
        }
    }
    return (
        <div>
            <h1 className='my-5 text-center'>HOME</h1>
            <Row className='mb-3'>
                <Col>
                    <Form onSubmit={onSubmit}>
                        <InputGroup>
                            <Form.Control onChange={(e) => setQuery(e.target.value)} value={query} />
                            <Button type="submit"></Button>
                        </InputGroup>
                    </Form>
                </Col>
                <Col></Col>
                <Col></Col>
            </Row>
            <Row>
                {document.map(doc =>                       //  db   !
                    <Col g={2} md={3} xs={6} className='mb-3' key={doc.isbn}>
                        <Card>
                            <Card.Body>
                                <BookPage book ={doc}/>
                            </Card.Body>
                            <Card.Footer>
                                <div className='text-truncate' alt=''>{doc.title}</div>
                                <div>{doc.sale_price}</div>
                            </Card.Footer>
                        </Card>
                    </Col>
                )}
            </Row>
            <div className='text-center mt-4'>
                <Button disabled={page === 1} onClick={() => setPage(page - 1)}></Button>
                <span>{page}/{last}</span>
                <Button disabled={page === last} onClick={() => setPage(page + 1)}></Button>
            </div>
        </div>
    )
}
export default HomePage
